<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
				<div class="c-bg"></div>
				<Map :params="mapParams" class="map"></Map>
      </div>
      <div class="w-right">
        <div class="r-top">
          <div class="t-left">
            <ctitle :title="'信访行为转化漏斗'"></ctitle>
          </div>
          <div class="t-right">
            <ctitle :title="'年度三率指数'"></ctitle>
            <div class="echarts">
              <radar class="chart" :params="zyzbRadarParams"></radar>
            </div>
          </div>
        </div>
        <div class="r-bottom">
          <ctitle :title="'信访人列表'"></ctitle>
          <div class="list">
            <div class="btn">预警人员名称</div>
            <ul>
              <li v-for="(item, index) in xfrList" 
              @click="changeXfr(index)"
              :class="index === defaultXfr ? 'colorful': ''"
              :key="index">{{item.name}}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { get_http } from "@/api/index";
  import ctitle from "@/components/title/index";
  import Map from "@/components/map/Mapyn";
  import radar from "@/components/radar/radar";
  export default {
    name: 'ycxs',
    components: {
      ctitle,
      Map,
      radar
    },
    data() {
      return {
        xfrList: [],
        defaultXfr: 0,
        // 地图
        mapParams: {
          canvasStyle: {
            width: "100%",
            height: "98%",
          },
          data: [],
          options: {
            borderColor: '#77608e',
            scatterColor: '#3cfff8',
            emphasisColor: '#ffd200',
            normalAreaColor: '#649dfb',
            emphasisAreaColor: '#66e8ff',
            emphasisBorderColor: '#fff',
            emphasis: {
              borderWidth: 2,
              shadowColor: '#1f4ea8',
              shadowBlur: 15,
              fontSize: 16
            },
            isHome: false,
            JSON: "530000",
            xName: "name",
						yName: "value",
						top: 0,
            map: {
              "value": "信访量"
            },
            carousel: false
          }
        },
        //2.年度三率指数
        zyzbRadarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            center: ['45%', '60%'],
            radius: '60%'
          }
        },
      }
    },
    mounted() {
      this.getMapAPI()
    },
    methods: {
      changMenu(item) {
        this.selectName = item;
      },
      changeXfr(index) {
        this.defaultXfr = index;
      },
      getMapAPI() {
        // 1.地图
        get_http("xfxwfxyjXwzhyjMap", {
          // shej_02: this.shej_02,
          // shij_02: this.shij_02,
          // xj_02: this.xj_02,
          // name: this.nameP,
        })
        .then(res => {
          this.mapParams.data = res.data.data;
          // this.mapFlag = true;
        })
        .catch(err => {
          console.log(err);
        });
        this.getAPI(this.year)
      },
      getAPI(year) {
        // 2.年度三率指数
        get_http("xfxwfxyjXwzhyjNdslzs", {year: year})
        .then(res => {
          this.zyzbRadarParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });

        // 3.信访人列表
        get_http("xfxwfxyjXwzhyjXfrlb", {year: year})
        .then(res => {
          this.xfrList = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val);
      }
    }
  }
</script>

<style lang="stylus" scoped>
.wrapper
  position relative
  top 0.45rem
  height calc(100% - 45px)
  .content
    width 100%
    height 100%
    display flex
    padding .2rem .3rem
    box-sizing border-box
    .w-left
      position relative
      flex 1
      height 100%
      display flex
      flex-direction column
      .map-chart
        background none
        position absolute
        top -45px
      .c-bg
        position absolute
        width 100%
        height 100%
        z-index 0
        top 0
        left 0
        background url("../../../assets/map-bg.png") no-repeat center bottom
    .w-right
      flex 1.2
      display flex
      flex-direction column
      .r-top
        flex 1
        display flex
        .t-left
          flex 1.5
        .t-right
          flex 1
          .echarts
            width 100%
            height 100%
      .r-bottom
        display flex
        flex-direction column
        flex 1
        .list
          margin 0 auto
          flex 1
          background url("../img/xfrlb-bg.png") no-repeat center bottom 
          position relative
          width 7.8rem
          display flex
          align-items flex-end
          margin-bottom 0.2rem
          .btn
            width 1.76rem
            border 1px solid #29adff
            color #fff
            font-weight 700
            text-shadow 0 0 10px #fff
            border-radius 0.3rem
            background-color rgba(0, 0, 0, 0.502)
            line-height 0.3rem
            text-align center
            height 0.32rem
            font-size 0.16rem
            cursor pointer
            position absolute
            top -0.25rem
            right 1.5%
            &:after
              content ''
              background-color rgb(243, 255, 58)
              position absolute
              width 70%
              height 0.03rem
              top 100%
              left 15%
          ul
            display flex
            flex-wrap wrap
            text-align center
            margin-bottom 0.3rem
            margin-left 0.25rem
            li
              width 16%
              color #9aedfb
              font-size 16px
              font-weight bold
              line-height 34px
              cursor pointer
              margin-bottom 10px
              margin-left 20px
              border-radius 5px
            .colorful
              background rgba(255, 156, 0, 0.1)
              color #fff
              font-weight 700
              text-shadow 0 0 10px #fff
</style>
